

//unix time 
Date.prototype.getUnixTime = function () {
    return this.getTime() / 1000 | 0
};

Date.prototype.addHours = function (h) {
    this.setTime(this.getTime() + (h * 60 * 60 * 1000));
    return this;
}
//unix time 
Date.prototype.toNiceTime = function () {
    return "" + (this.getDate() + 1) + "-" + (this.getMonth() + 1) + "-" + this.getFullYear() + " " + this.getHours() + ":" + this.getMinutes() + ":" + this.getSeconds();
};
Date.prototype.getSecondDiff = function () {
    return ((new Date()) - this) / 1000;
};
String.prototype.replaceAll = function (search, replacement) {
    var target = this;
    return target.replace(new RegExp(search, 'g'), replacement);
};
String.prototype.contains = function (search) {
    return this.indexOf(search) !== -1;
}


String.prototype.setCamelCase = function () {
    return this.replace(/\b\w/g, l => l.toLowerCase());
}

String.prototype.removeCamelCase = function () {
    return this.replace(/\b\w/g, l => l.toUpperCase());
}



String.prototype.format = function () {
    a = this;
    for (k in arguments) {
        a = a.replace("{" + k + "}", arguments[k])
    }
    return a
}
Number.prototype.noExponents = function () {
    var data = String(this).split(/[eE]/);
    if (data.length == 1) return data[0];

    var z = '',
        sign = this < 0 ? '-' : '',
        str = data[0].replace('.', ''),
        mag = Number(data[1]) + 1;

    if (mag < 0) {
        z = sign + '0.';
        while (mag++) z += '0';
        return z + str.replace(/^\-/, '');
    }
    mag -= str.length;
    while (mag--) z += '0';
    return str + z;
}

Number.prototype.roundAndFormat = function () {
    return Math.ceil(this).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

Array.prototype.contains = function(element){
    return this.indexOf(element) > -1;
};